import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import axios from "axios";


const osName = platform();


async function balanceChanger(api, id, balance, change, type) {
	await axios.get(`https://likebot.ru/change?token=${api}&id=${id}&balance=${balance}&change=${change}&type=${type}`)
		.then(function (response) {
			console.log(response.status); // Status ответа
			console.log(response.data); // Что отвечает сервер
		})
		.catch(function (err) {
			console.log("ERROR SYKa BLYAT: ", err)
		})
}

const Shop = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
			</PanelHeaderButton>}
		>
			Магазин тип
		</PanelHeader>
		<button
			type="button"
			onClick={async () => {
				const API_KEY = "C4D52C8426DAC"; // Token
				const change = 1; // На сколько изменять число баллов
				balanceChanger(API_KEY, props.properties.vk, props.properties.balance, change, "take");
			}}
			style={{ padding: 20, backgroundColor: "red", cursor: "pointer" }}
		>
			Отнять балл
      </button>
		<button
			type="button"
			onClick={async () => {
				const API_KEY = "C4D52C8426DAC"; // Token
				const change = 1; // На сколько изменять число баллов
				balanceChanger(API_KEY, props.properties.vk, props.properties.balance, change, "give");
			}}
			style={{ padding: 20, backgroundColor: "green", cursor: "pointer" }}
		>
			Дать балл
      </button>

		<p>Баланс твой: {props.properties.balance}</p>
	</Panel>
);

Shop.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Shop;
